/* For pie chart */

:root {
  --size: 150px;
  --bord: 25px;
}

.chart {
  width: 150px;
  width: var(--size);
  height: 150px;
  height: var(--size);
  margin: 0em auto;
  border-radius: 50%;
  /* background-image: conic-gradient(#D5E300 var(--value), #103147 var(--value)); */
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chart::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 25px);
  width: calc(100% - var(--bord));
  height: calc(100% - 25px);
  height: calc(100% - var(--bord));
  background: #F1F1F1;
  border-radius: inherit;
}

p {
  position: relative;
  z-index: 1;
  font-size: 1em;
}

